<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <a-form-model-item label="需求组名称" prop="groupName">
              <a-input
                v-model="entity.groupName"
                placeholder="请输入需求组名称"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="所属部门" prop="department">
              <a-select
                v-model="entity.department"
                placeholder="请选择所属部门"
              >
                <a-select-option value="研发部">研发部</a-select-option>
                <a-select-option value="生产部">生产部</a-select-option>
                <a-select-option value="质量部">质量部</a-select-option>
                <a-select-option value="人力资源部">人力资源部</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="优先级" prop="priority">
              <a-select
                v-model="entity.priority"
                placeholder="请选择优先级"
              >
                <a-select-option value="高">高</a-select-option>
                <a-select-option value="中">中</a-select-option>
                <a-select-option value="低">低</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>

        <a-row :gutter="24">
          <a-col :sm="18">
            <a-form-model-item label="培训需求" prop="requirements">
              <a-select
                v-model="entity.requirements"
                mode="multiple"
                placeholder="请选择培训需求"
                style="width: 100%"
              >
                <a-select-option v-for="item in requirementOptions" :key="item.value" :value="item.value">
                  {{ item.label }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>

        <a-row :gutter="24">
          <a-col :sm="24">
            <a-form-model-item label="备注" prop="description">
              <a-textarea
                v-model="entity.description"
                :rows="4"
                placeholder="请输入备注信息"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <g-collapse headerName="其他信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <a-form-model-item label="创建人">
                <span>{{ entity.creator || 'admin' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="创建时间">
                <span>{{ entity.createTime || '2024-01-01 12:00:00' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="状态">
                <span>{{ entity.status || '待审核' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditTrainingRequirementGroup",
  mixins: [GNdoPageEditBase],

  data() {
    return {
      entity: {
        groupName: "",
        department: undefined,
        priority: undefined,
        requirements: [],
        description: "",
        creator: "",
        createTime: "",
        status: "",
      },
      requirementOptions: [
        { value: '1', label: '培训需求1' },
        { value: '2', label: '培训需求2' },
        { value: '3', label: '培训需求3' },
        { value: '4', label: '培训需求4' },
        { value: '5', label: '培训需求5' },
      ],
      rules: {
        groupName: [
          { required: true, message: "需求组名称不能为空", trigger: "change" },
          {
            pattern: /^[^\s]+(\s+[^\s]+)*$/,
            message: "名称不能包含空格",
            trigger: "change",
          },
        ],
        department: [
          { required: true, message: "请选择所属部门", trigger: "change" },
        ],
        priority: [
          { required: true, message: "请选择优先级", trigger: "change" },
        ],
        requirements: [
          { required: true, message: "请选择培训需求", trigger: "change" },
          { type: 'array', min: 1, message: "至少选择一个培训需求", trigger: "change" },
        ],
      },
    };
  },

  methods: {
    init() {
      this.loading = true;
      if (this.id) {
        // 模拟请求
        setTimeout(() => {
          this.loading = false;
          this.entity = {
            groupName: "测试培训需求组",
            department: "研发部",
            priority: "高",
            requirements: ['1', '2'],
            description: "这是一个测试培训需求组",
            creator: "admin",
            createTime: "2024-01-01 12:00:00",
            status: "待审核",
          };
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },

    resetFormData() {
      this.entity = {
        groupName: "",
        department: undefined,
        priority: undefined,
        requirements: [],
        description: "",
        creator: "",
        createTime: "",
        status: "",
      };
    },

    async save() {
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.groupName);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },

    inChange(e) {
      console.log(e.target.value);
    },
  },
};
</script>

<style lang="scss" scoped>
.card {
  background: #fff;
  padding: 24px;
  margin-bottom: 24px;
}
</style>